.hljs {
      width: 100%;
      box-sizing: border-box;
      display: block;
      overflow-x: auto;
      padding:20px 0;
      color: white;
}

.hljs-comment,
.hljs-quote {
      color: #bc9458;
      font-style: italic
}

.hljs-keyword,
.hljs-selector-tag {
      color: #c26230
}

.hljs-string,
.hljs-number,
.hljs-regexp,
.hljs-variable,
.hljs-template-variable {
      color: #a5c261
}

.hljs-subst {
      color: #519f50
}

.hljs-tag,
.hljs-name {
      color: #e8bf6a
}

.hljs-type {
      color: #da4939
}

.hljs-symbol,
.hljs-bullet,
.hljs-built_in,
.hljs-builtin-name,
.hljs-attr,
.hljs-link {
      color: #6d9cbe
}

.hljs-params {
      color: #d0d0ff
}

.hljs-attribute {
      color: #cda869
}

.hljs-meta {
      color: #9b859d
}

.hljs-title,
.hljs-section {
      color: #ffc66d
}

.hljs-addition {
      background-color: #144212;
      color: #e6e1dc;
      display: inline-block;
      width: 100%
}

.hljs-deletion {
      background-color: #600;
      color: #e6e1dc;
      display: inline-block;
      width: 100%
}

.hljs-selector-class {
      color: #9b703f
}

.hljs-selector-id {
      color: #8b98ab
}

.hljs-emphasis {
      font-style: italic
}

.hljs-strong {
      font-weight: bold
}

.hljs-link {
      text-decoration: underline
}